import React, { useState } from "react";

import { ButtonsPanel, ToggleButton } from "../components";
import { Configs, View } from "../types";

export interface SettingsViewProps {
  view: View;
  configs: Configs;
  onConfigsSave: (newConfigs: Configs) => void;
  onBackButtonClicked: () => void;
  onLearnMoreClicked: () => void;
}

export const SettingsView = (props: SettingsViewProps): JSX.Element => {
  const { view, configs, onConfigsSave, onBackButtonClicked, onLearnMoreClicked } = props;

  const [coordinateInputState, setCoordinateInputState] = useState(configs.coordinates);

  return (
    <div>
      <ButtonsPanel view={view} onBackButtonClicked={onBackButtonClicked} />
      <div className="container">
        <div className="m-5" />
        <h1>Settings</h1>
        <p className="d-inline-block">
          This app does not collect or share any data with 3rd party services. All data are stored and process on this
          device.
        </p>{" "}
        <button className="btn btn-light" type="button" onClick={onLearnMoreClicked}>
          <span className="mx-2">Learn More</span>
        </button>
        <div>
          <h2>Prayer Times</h2>
          <div className="card my-3">
            <div className="card-body p-3 ">
              <div className="d-flex justify-content-between align-items-center">
                <div>Show Prayer times</div>
                <ToggleButton
                  checked={configs.showPrayerTimes}
                  onChange={(checked) => {
                    onConfigsSave({ ...configs, showPrayerTimes: checked });
                  }}
                />
              </div>
            </div>
          </div>
          {configs.showPrayerTimes && (
            <div className="card my-3">
              <div className="card-body p-3 ">
                <div className="mb-3">Coordinates (in decimal degrees format)</div>
                <div className="d-flex">
                  <input
                    type="text"
                    className="form-control"
                    placeholder="E.g. 51.045634, -114.057501"
                    value={coordinateInputState}
                    onChange={(event) => {
                      setCoordinateInputState(event.target.value);
                    }}
                  />
                  <div className="mx-2" />
                  <button
                    className="btn btn-primary d-flex align-items-center"
                    type="button"
                    onClick={() => {
                      onConfigsSave({ ...configs, coordinates: coordinateInputState });
                    }}
                  >
                    <i className="fa fa-save" />
                    <span className="mx-2">Save</span>
                  </button>
                </div>
              </div>
            </div>
          )}
        </div>
        <div className="m-5" />
      </div>
    </div>
  );
};

export default SettingsView;
