import React, { useState } from "react";
import "./App.css";
import "./bin/bootstrap.min.css";
import "@fortawesome/fontawesome-free/css/all.css";
import { BismillahView, SettingsView } from "./views";
import { Configs, CONFIGS_LOCAL_STORAGE_KEY, DEFAULT_CONFIGS, View } from "./types";
import AboutPage from "./views/AboutPage";

const App = (): JSX.Element => {
  let dataFromLocalStorage: Configs = JSON.parse(localStorage.getItem(CONFIGS_LOCAL_STORAGE_KEY) as string);

  if (!dataFromLocalStorage) {
    dataFromLocalStorage = DEFAULT_CONFIGS;
    localStorage.clear();
    localStorage.setItem(CONFIGS_LOCAL_STORAGE_KEY, JSON.stringify(dataFromLocalStorage));
  }

  const [configs, setConfigs] = useState<Configs>(dataFromLocalStorage);
  const [view, setView] = useState<View>(View.BISMILLAH_TAB);

  let viewComponent = (
    <BismillahView
      view={view}
      configs={configs}
      onSettingsButtonClicked={() => {
        setView(View.SETTINGS);
      }}
      onAboutButtonClicked={() => {
        setView(View.ABOUT);
      }}
    />
  );

  switch (view) {
    case View.SETTINGS:
      viewComponent = (
        <SettingsView
          view={view}
          onBackButtonClicked={() => {
            setView(View.BISMILLAH_TAB);
          }}
          configs={configs}
          onConfigsSave={(newConfigs) => {
            setConfigs(newConfigs);
            localStorage.setItem(CONFIGS_LOCAL_STORAGE_KEY, JSON.stringify(newConfigs));
          }}
          onLearnMoreClicked={() => setView(View.ABOUT)}
        />
      );
      break;
    case View.ABOUT:
      viewComponent = (
        <AboutPage
          view={view}
          onBackButtonClicked={() => {
            setView(View.BISMILLAH_TAB);
          }}
        />
      );
      break;
    case View.BISMILLAH_TAB:
    default:
      break;
  }

  return viewComponent;
};

export default App;
