import React from "react";
import { View } from "../types";

export interface ButtonsPanelProps {
  view: View;
  onSettingsButtonClicked?: () => void;
  onBackButtonClicked?: () => void;
}

export const ButtonsPanel = (props: ButtonsPanelProps): JSX.Element => {
  const { view, onSettingsButtonClicked, onBackButtonClicked } = props;

  const showSettingsButton = view === View.BISMILLAH_TAB;
  const showBackButton = view === View.SETTINGS || view === View.ABOUT;

  return (
    <div className="p-3">
      <div className="d-flex justify-content-between">
        {/* Left */}
        <div>
          {showBackButton && (
            <button className="btn btn-light" type="button" onClick={onBackButtonClicked}>
              <i className="fas fa-arrow-left" />
              <span className="mx-2">Back</span>
            </button>
          )}
        </div>

        {/* Right */}
        <div>
          {showSettingsButton && (
            <button className="btn btn-light" type="button" onClick={onSettingsButtonClicked}>
              <i className="fas fa-cog" />
              <span className="mx-2">Settings</span>
            </button>
          )}
        </div>
      </div>
    </div>
  );
};

export default ButtonsPanel;
