import React from "react";
import BismillahText from "../components/BismillahText";
import SearchBar from "../components/SearchBar";
import { ButtonsPanel, PrayerTimes } from "../components";
import { Configs, View } from "../types";

export interface BismillahViewProps {
  view: View;
  configs: Configs;
  onSettingsButtonClicked: () => void;
  onAboutButtonClicked: () => void;
}

export const BismillahView = (props: BismillahViewProps): JSX.Element => {
  const { view, configs, onSettingsButtonClicked, onAboutButtonClicked } = props;

  return (
    <div>
      <ButtonsPanel view={view} onSettingsButtonClicked={onSettingsButtonClicked} />
      <div className="container">
        <div className="d-flex flex-column align-items-center justify-content-center" style={{ height: "80vh" }}>
          <div className="mb-4">
            <BismillahText />
          </div>
          {configs.showPrayerTimes && (
            <div className="mb-3 w-100">
              <PrayerTimes coordinates={configs.coordinates} onSettingsButtonClicked={onSettingsButtonClicked} />
            </div>
          )}
          <div className="w-100">
            <SearchBar />
          </div>
        </div>
        <div className="d-flex flex-column align-items-center">
          <div> 2022 Arshi Annafi</div>
          <div>
            <button className="btn btn-link" type="button" onClick={onAboutButtonClicked}>
              <span className="mx-2">Privacy, Attributions, and FAQs</span>
            </button>
          </div>
        </div>
      </div>
    </div>
  );
};

export default BismillahView;
