import React from "react";

import adhan from "../../bin/adhan";
import moment from "../../bin/moment";
import { CoordinatesError, getLatitudeAndLongitude } from "../../types";
import Warning from "./Warning";
import CoordinatesMalformedMessage from "./CoordinatesMalformedMessage";
import DefaultLocationMessage from "./DefaultLocationMessage";

export interface PrayerTimesProps {
  coordinates: string;
  onSettingsButtonClicked?: () => void;
}

export const PrayerTimes = (props: PrayerTimesProps): JSX.Element => {
  const { coordinates, onSettingsButtonClicked } = props;

  const { latitude, longitude, error } = getLatitudeAndLongitude(coordinates);

  // TODO @arshi add settings to adjust calculation method
  const params = adhan.CalculationMethod.NorthAmerica();

  // TODO @arshi add settings to adjust offsets
  params.adjustments = {
    fajr: 0,
    sunrise: 0,
    dhuhr: 0,
    asr: 0,
    maghrib: 0,
    isha: 0,
  };

  const prayerTimes = new adhan.PrayerTimes(new adhan.Coordinates(latitude, longitude), new Date(), params);

  return (
    <div>
      <div className="d-flex flex-column flex-sm-row justify-content-center text-center">
        <div className="p-2"> {moment(prayerTimes.fajr).format("HH:mm")}</div>
        <div className="p-2"> {moment(prayerTimes.sunrise).format("HH:mm")}</div>
        <div className="p-2"> {moment(prayerTimes.dhuhr).format("HH:mm")}</div>
        <div className="p-2"> {moment(prayerTimes.asr).format("HH:mm")}</div>
        <div className="p-2"> {moment(prayerTimes.maghrib).format("HH:mm")}</div>
        <div className="p-2"> {moment(prayerTimes.isha).format("HH:mm")}</div>
      </div>

      {error === CoordinatesError.DEFAULT_COORDINATES && (
        <Warning>
          <DefaultLocationMessage
            onSettingsButtonClicked={() => {
              onSettingsButtonClicked?.();
            }}
          />
        </Warning>
      )}

      {error === CoordinatesError.IMPROPER_FORMAT && (
        <Warning>
          <CoordinatesMalformedMessage
            onSettingsButtonClicked={() => {
              onSettingsButtonClicked?.();
            }}
          />
        </Warning>
      )}
    </div>
  );
};

export default PrayerTimes;
