import React from "react";
import { ButtonsPanel } from "../components";
import { View } from "../types";

interface NotesViewProps {
  view: View;
  onBackButtonClicked: () => void;
}

interface AttributionProps {
  title: string;
  author: string;
  url: string;
}

const Cite = (props: AttributionProps): JSX.Element => {
  const { title, author, url } = props;
  return (
    <div>
      {`${author}. ${title} [Online]. Available: `}
      <a href={url}>{url}</a>.
    </div>
  );
};

const Policies = (): JSX.Element => (
  <div>
    <h1>Policies</h1>
    <div className="m-4" />
    <h2>Data</h2>
    <p>
      Data is stored locally (
      <a href="https://developer.mozilla.org/en-US/docs/Web/API/Window/localStorage">
        https://developer.mozilla.org/en-US/docs/Web/API/Window/localStorage
      </a>
      ) on the device and does not leave your device.
    </p>
    <p>
      Follow instructions on this article (
      <a href="https://intercom.help/scoutpad/en/articles/3478364-how-to-clear-local-storage-of-web-browser">
        https://intercom.help/scoutpad/en/articles/3478364-how-to-clear-local-storage-of-web-browser
      </a>
      ) to clear all data stored by this app on this device.
    </p>
  </div>
);

const Attributions = (): JSX.Element => (
  <div>
    <h1>Attributions</h1>
    <div className="m-4" />
    <ul>
      <li>
        <Cite
          author="Batoul Apps et al"
          title="Adhan JavaScript (adhan-js v4.2.1)"
          url="https://github.com/batoulapps/adhan-js/"
        />
      </li>
      <li>
        <Cite author="React" title="React v17.0.2" url="https://reactjs.org/" />
      </li>
      <li>
        <Cite author="Bootstrap" title="Bootstrap v4.3.1" url="https://getbootstrap.com/" />
      </li>
      <li>
        <Cite author="jQuery" title="jQuery v3.5.1" url="https://jquery.com/" />
      </li>
      <li>
        <Cite author="Moment.js" title="Moment.js v2.29.1" url="https://momentjs.com/" />
      </li>
      <li>
        <Cite author="Fontawesome" title="fontawesome-free v5.15.4" url="https://fontawesome.com/" />
      </li>
    </ul>
    <div className="m-5" />
  </div>
);

const FAQs = (): JSX.Element => (
  <div>
    <h1>FAQs</h1>
    <div className="m-4" />
    <h2>How to get coordinates</h2>
    To get coordinates,
    <ol>
      <li>
        go to <a href="https://www.google.com/maps/">Google Maps</a>,
      </li>
      <li>zoom to the location that you would like to use to calculate the prayer times,</li>
      <li>right click on the map, and</li>
      <li>click on the coordinate to copy.</li>
    </ol>
    You can then paste the values in &quot;Settings&quot; and click the &quot;Save&quot; button.
  </div>
);

const AboutPage = (props: NotesViewProps): JSX.Element => {
  const { view, onBackButtonClicked } = props;
  return (
    <div>
      <div>
        <ButtonsPanel view={view} onBackButtonClicked={onBackButtonClicked} />
        <div className="container">
          <div className="m-5" />
          <Policies />
          <div className="m-5" />
          <Attributions />
          <div className="m-5" />
          <FAQs />
          <div className="m-5" />
        </div>
      </div>
    </div>
  );
};

export default AboutPage;
