/* eslint-disable no-unused-vars */
export enum CoordinatesError {
  DEFAULT_COORDINATES,
  IMPROPER_FORMAT,
}

export const getLatitudeAndLongitude = (
  coordinatesString: string,
): {
  latitude: number;
  longitude: number;
  error: CoordinatesError | null;
} => {
  if (coordinatesString === "" || coordinatesString === "0, 0") {
    return { latitude: 0, longitude: 0, error: CoordinatesError.DEFAULT_COORDINATES };
  }

  const coordinates = coordinatesString.split(", ");
  const latitude = Number(coordinates[0]);
  const longitude = Number(coordinates[1]);

  if (coordinates.length !== 2 || !latitude || !longitude)
    return { latitude: 0, longitude: 0, error: CoordinatesError.IMPROPER_FORMAT };

  return { latitude, longitude, error: null };
};
